<template>
  <div>
    <table class="table table-bordered">
      <!-- <tr>
        <th>글 번호</th>
        <td><input type="text" id="no" ref="no" v-model="qna.no" readonly /></td>
      </tr> -->
      <tr>
        <th>제목</th>
        <td>
          <input type="text" id="title" ref="title" v-model="qna.title" />
        </td>
      </tr>
      <tr>
        <th>작성자</th>
        <td>
          <input type="text" id="id" ref="id" v-model="qna.id" />
        </td>
      </tr>
      <!-- <tr>
        <th>작성일자</th>
        <td>
          <input type="text" id="regtime" ref="regtime" v-model="qna.regtime" readonly />
        </td>
      </tr>
      <tr>
        <th>답변완료</th>
        <td>
          <input type="text" id="completed" ref="completed" v-model="qna.completed" readonly />
        </td>
      </tr> -->
      <tr>
        <th colspan="2">문의사항</th>
      </tr>
      <tr>
        <td colspan="2">
          <textarea
            id="question"
            cols="46"
            rows="10"
            ref="question"
            v-model="qna.question"
          ></textarea>
        </td>
      </tr>
      <!-- <tr>
        <th colspan="2">답변</th>
      </tr>
      <tr>
        <td colspan="2">
          <textarea id="answer" cols="46" rows="10" ref="answer" v-model="qna.answer"></textarea>
        </td>
      </tr> -->
      <tr>
        <td>
          <button class="btn btn-primary" @click="updateHandler">수정</button>
          <router-link :to="{ name: 'QnAListView' }">
            <button class="btn btn-primary">목록</button>
          </router-link>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import http from '@/api/http';
export default {
  data() {
    return {
      no: '',
      qna: Object,
    };
  },
  created() {
    //DOM객체와 연결되었고 Vue의 속성들도 초기화 된 상태
    this.no = this.$route.query.no;
    console.log('Modify.html..................no]', this.no);
    http
      .get(`qna/${this.no}`)
      .then(({ data }) => {
        console.log('data......', data);
        this.qna = data;
      })
      .catch(({ data }) => {
        alert(data);
      });
  },
  methods: {
    updateHandler() {
      //데이타 검증
      let err = false;
      let msg = '';
      console.log('updateHandler.................', this.qna);
      !this.qna.no && ((msg = '글 번호를 입력해주세요'), (err = true), this.$refs.no.focus());
      !err & !this.qna.title &&
        ((msg = '제목을 입력해주세요'), (err = true), this.$refs.title.focus());
      !err & !this.qna.question &&
        ((msg = '문의사항을 입력해주세요'), (err = true), this.$refs.question.focus());
      if (err) {
        alert(msg);
      } else {
        //수정하기
        console.log(this.qna);
        http
          .put('qna', this.qna)
          .then(({ data }) => {
            if (data === 'success') {
              alert('수정 완료');
              this.$router.push({
                name: 'QnADetailView',
                query: { no: this.qna.no },
              });
            }
          })
          .catch(({ data }) => {
            alert(data);
          });
      }
    },
  },
};
</script>

<style></style>
