<template>
  <div>
    <table class="table table-bordered">
      <tr>
        <th>글 번호</th>
        <td v-text="qna.no"></td>
      </tr>
      <tr>
        <th>제목</th>
        <td v-text="qna.title"></td>
      </tr>
      <tr>
        <th>작성자</th>
        <td v-text="qna.id"></td>
      </tr>
      <tr>
        <th>작성일자</th>
        <td v-text="qna.regtime"></td>
      </tr>
      <!-- <tr>
        <th>답변완료</th>
        <td v-text="qna.completed"></td>
      </tr> -->
      <tr>
        <th colspan="2">문의사항</th>
      </tr>
      <tr>
        <th colspan="2"><pre v-text="qna.question"></pre></th>
      </tr>
      <tr>
        <th colspan="2">답변</th>
      </tr>
      <tr>
        <th colspan="2"><pre v-text="qna.answer"></pre></th>
      </tr>
      <tr>
        <td>
          <div>
            <button class="btn btn-primary" @click="moveHandler">목록</button>
            <router-link :to="{ name: 'QnAModifyView', query: { no: qna.no } }">
              <button class="btn btn-primary">수정</button>
            </router-link>
            <button class="btn btn-primary" @click="removeHandler">삭제</button>
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import http from '@/api/http';

export default {
  data() {
    return {
      no: '',
      qna: {},
    };
  },
  created() {
    //DOM객체와 연결되었고 Vue의 속성들도 초기화 된 상태
    this.no = this.$route.query.no;
    console.log('view.html..................no]', this.no);
    http
      .get(`qna/${this.no}`)
      .then(({ data }) => {
        this.qna = data;
      })
      .catch(({ data }) => {
        alert(data);
      });
  },
  methods: {
    moveHandler() {
      this.$router.push({ name: 'QnAListView' });
    },
    removeHandler() {
      console.log('view.html..................삭제]', this.no);
      http
        .delete(`qna/${this.no}`)
        .then(({ data }) => {
          if (data == 'success') {
            alert('삭제 성공');
            this.moveHandler();
          }
        })
        .catch(({ data }) => {
          alert(data);
        });
    },
  },
};
</script>

<style></style>
