<template>
  <div>
    <table class="table table-boardered">
      <!-- <tr>
        <th>글 번호</th>
        <td><input type="text" id="no" ref="no" v-model="qna.no" /></td>
      </tr> -->
      <tr>
        <th>제목</th>
        <td>
          <input type="text" id="title" ref="title" v-model="qna.title" />
        </td>
      </tr>
      <tr>
        <th>작성자</th>
        <td>
          <input type="text" id="id" ref="id" v-model="qna.id" />
        </td>
      </tr>
      <!-- <tr>
        <th>작성일자</th>
        <td>
          <input type="text" id="regtime" ref="regtime" v-model="qna.regtime" />
        </td>
      </tr> -->
      <!-- <tr>
        <th>답변완료</th>
        <td>
          <input type="text" id="completed" ref="completed" v-model="qna.completed" />
        </td>
      </tr> -->
      <tr>
        <th colspan="2">문의사항</th>
      </tr>
      <tr>
        <td colspan="2">
          <textarea
            id="question"
            cols="46"
            rows="10"
            ref="question"
            v-model="qna.question"
          ></textarea>
        </td>
      </tr>
      <!-- <tr>
        <th colspan="2">답변</th>
      </tr>
      <tr>
        <td colspan="2">
          <textarea id="answer" cols="46" rows="10" ref="answer" v-model="qna.answer"></textarea>
        </td>
      </tr> -->
      <tr>
        <td colspan="2">
          <div class="text-center">
            <button class="btn btn-primary" @click="createHandler">등록</button>
            <router-link :to="{ name: 'QnAListView' }">
              <button class="btn btn-primary">목록</button>
            </router-link>
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import http from '@/api/http';
export default {
  data() {
    return {
      qna: {
        // no: '',
        title: '',
        id: '',
        question: '',
        // regtime: '',
        // completed: '',
        // answer: '',
      },
    };
  },
  methods: {
    moveHandler() {
      this.$router.push({ name: 'QnAListView' });
    },
    createHandler() {
      //데이타 검증
      let err = false;
      let msg = '';

      // if (this.qna.no == '') {
      //   err = true;
      //   msg = '책 번호를 입력해 주세요';
      //   // $refs : Vue 객체에서 DOM 객체에 접근 할 때
      //   this.$refs.no.focus();
      // }

      if (!err && this.qna.title == '') {
        err = true;
        msg = '제목을 입력해 주세요';
        this.$refs.title.focus();
      }

      if (!err && this.qna.id == '') {
        err = true;
        msg = '작성자를 입력해 주세요';
        this.$refs.id.focus();
      }

      if (!err && this.qna.question == '') {
        err = true;
        msg = '문의내용을 입력해 주세요';
        this.$refs.question.focus();
      }

      if (err) {
        alert(msg);
      } else {
        //등록 처리
        //localStorage에 이미 저장된 책 목록이 있는지 검사

        let newQnA = {
          // no: this.qna.no,
          id: this.qna.id,
          title: this.qna.title,
          question: this.qna.question,
          // regtime: this.qna.regtime,
          // completed: this.qna.completed,
          // answer: this.qna.answer,
        };

        http.post('qna', newQnA).then(({ data }) => {
          if (data === 'success') {
            alert('등록 완료');
            this.moveHandler();
          }
        });
      }
    },
  },
};
</script>

<style></style>
