<template>
  <div>
    <div class="row">
      <div class="col-2"></div>

      <div class="col-2">
        <select class="form-control" v-model="key">
          <option value="all">--선택하세요--</option>
          <option v-for="(k, index) in keys" :key="index" :value="k">
            {{ k }}
          </option>
        </select>
      </div>
      <div class="col-4">
        <input type="text" class="form-control" v-model="word" />
      </div>
      <div class="col-2">
        <button class="btn btn-primary" @click="searchQnA">검색</button>
      </div>
      <div class="col-2"></div>
    </div>
    <div v-if="qnas.length > 0">
      <table class="table table-bordered">
        <colgroup>
          <col width="5%" />
          <col width="45%" />
          <col width="15%" />
          <col width="25%" />
          <col width="10%" />
        </colgroup>
        <tr>
          <th>글 번호</th>
          <th>문의사항</th>
          <th>작성자</th>
          <th>작성일자</th>
          <!-- <th>답변완료</th> -->
        </tr>
        <tr v-for="qna in qnas" :key="qna.no">
          <td>{{ qna.no }}</td>
          <td>
            <router-link :to="{ name: 'QnADetailView', query: { no: qna.no } }">{{
              qna.title
            }}</router-link>
          </td>
          <td v-text="qna.id"></td>
          <td v-text="qna.regtime"></td>
          <!-- <td v-text="qna.completed"></td> -->
        </tr>
      </table>
    </div>
    <div v-else>등록된 문의사항이 없습니다.</div>
  </div>
</template>

<script>
import http from '@/api/http';

export default {
  data() {
    return {
      qnas: [],
      word: '',
      keys: ['no', 'title', 'id'],
      key: 'all',
    };
  },
  created() {
    this.searchQnA();
  },
  methods: {
    searchQnA() {
      console.log('key.......', this.key);
      const url = `qna?key=${this.key}&pageNo=1&word=${this.word}`;
      http.get(url).then(({ data }) => {
        console.log('응답 데이타', data);
        this.qnas = data;
      });
    },
  },
};
</script>

